import React from "react";
import { useState } from "react";

import { Link } from "react-router-dom";

import "../../App.css";
import "./Login.css";

import ScrollToTop from "../../ScrollTop";

export default Login;

function Login(): JSX.Element {
  const [id, setId] = useState<string>("");
  const [password, setPassword] = useState<string>("");

  const onEmailHandler = (e: {
    currentTarget: { value: React.SetStateAction<string> };
  }) => {
    setId(e.currentTarget.value);
  };
  const onPasswordHandler = (e: {
    currentTarget: { value: React.SetStateAction<string> };
  }) => {
    setPassword(e.currentTarget.value);
  };
  const onSubmitHandler = (e: React.MouseEvent) => {
    e.preventDefault();
    setId("");
    setPassword("");

    // console.log('Email', Email);
    // console.log('Password', Password);
  };
  return (
    <div id="sub">
      <ScrollToTop />
      <div className="loginWrap">
        <div className="widthHolder">
          <form>
            <div className="loginBox">
              <h3 className="loginBoxTitle">Login</h3>
              <fieldset>
                <legend></legend>
                <label className="id" title="">
                  <input
                    id="member_id"
                    name="member_id"
                    fw-filter="isFill"
                    fw-label=""
                    fw-msg=""
                    className="inputTypeText"
                    placeholder=""
                    value={id}
                    onChange={onEmailHandler}
                    type="text"
                  />
                </label>
                <label className="pw" title="">
                  <input
                    id="member_passwd"
                    name="member_passwd"
                    fw-filter="isFill&amp;isMin[4]&amp;isMax[16]"
                    fw-label=""
                    fw-msg=""
                    autoComplete="off"
                    value={password}
                    onChange={onPasswordHandler}
                    type="password"
                  />
                </label>
                <Link to={""} onClick={onSubmitHandler} className="loginBtn">
                  
                </Link>
                <p className="loginBoxTxtSmall">
                    ?       
                    .
                </p>
                <ul>
                  <li>
                    <Link to={""}> </Link>
                  </li>
                  <li>
                    <Link to={""}></Link>
                  </li>
                  <li>
                    <Link to={""}></Link>
                  </li>
                  <li>
                    <Link to={""}></Link>
                  </li>
                </ul>
              </fieldset>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}
